import {UsersReducerType, UsersType} from "./store";



export type ActionsTypeUsers = ReturnType<typeof followAC> | ReturnType<typeof unFollowAC> | ReturnType<typeof setUsersAc>

let initialState: UsersReducerType = {
    users: [] as Array<UsersType>
}

const usersReducer = (state =initialState, action: ActionsTypeUsers) => {
        switch (action.type) {
            case "FOLLOW": {
                return  {...state, users: state.users.map(u => {
                    if (u.id === action.userId) {
                        return {...u, followed: true}
                    }
                    return u
                    })}
            }
            case "UNFOLLOW":{
                return  {...state, users: state.users.map(u => {
                        if (u.id === action.userId) {
                            return {...u, followed: false}
                        }
                        return u
                    })}

            }
            case "SET_USERS": {
                return {...state, users: [...state.users, ...action.users]}
            }

            default: return {...state}
        }
}

export const followAC = (userId: string) => ({type: 'FOLLOW', userId}) as const;
export const unFollowAC = (userId: string) => ({type: 'UNFOLLOW', userId }) as const;
export const setUsersAc = (users: Array<UsersType>) => ({type: 'SET_USERS', users }) as const;

export default usersReducer;