import React from 'react';
import {addMessageAC, updateNewMessageTextAC} from "../../redux/dialogs-reducer"
import Dialogs from "./Dialogs";
import {connect} from "react-redux";
import {ActionsType} from "../../redux/redux-store";
import {RootStateType} from "../../redux/store";


let mapStateToProps = (state: RootStateType) => {
    return {
        dialogs: state.dialogsPage.dialogs,
        messages: state.dialogsPage.messages,
        newMessageText: state.dialogsPage.newMessageText
    }
}
let mapDispatchStateToProps = (dispatch: (action: ActionsType) => void ) => {
    return {
        updateNewMessageText: (message: string) => {
            dispatch(updateNewMessageTextAC(message))
        },
        addMessage: () => {
            dispatch(addMessageAC())
        }
    }
}

const DialogsContainer = connect(mapStateToProps, mapDispatchStateToProps)(Dialogs);

export default DialogsContainer;
