import {combineReducers, createStore} from "redux";
import profileReducer, {ActionsTypeProfile} from "./profile-reducer";
import dialogsReducer, {ActionsTypeDialogs} from "./dialogs-reducer";
import sidebarReducer from "./sidebar-reducer";
import usersReducer, {ActionsTypeUsers} from "./users-reducer";



export type ActionsType = ActionsTypeDialogs | ActionsTypeProfile |  ActionsTypeUsers

let reducers = combineReducers({
    profilePage: profileReducer,
    dialogsPage: dialogsReducer,
    usersPage: usersReducer,
    sidebar: sidebarReducer,
})

let store = createStore(reducers)


export default store;