import React, {ChangeEvent} from 'react';
import s from './Dialogs.module.css';
import DialogItem from './DialogsItem/DialogItem'
import Message from './Message/Message'

import { DialogsType, MessagesType} from "../../redux/store";

type PropsType = {
    dialogs: Array<DialogsType>
    messages: Array<MessagesType>
    updateNewMessageText: (message: string) => void
    addMessage: () => void
    newMessageText: string
}

const Dialogs = (props: PropsType) => {

let dialogsElements = props.dialogs.map(dialog => <DialogItem name={dialog.name} key={dialog.id} id={dialog.id} img={dialog.img} />);

let messageElements = props.messages.map(message => <Message message={message.message} key={message.id} id={message.id} />);


    let onMessageChange = (e: ChangeEvent<HTMLTextAreaElement>) =>{
        props.updateNewMessageText(e.currentTarget.value)

    }

    return (
        <div className={s.dialogs}>
            <div className={s.dialogsItem}>
                {dialogsElements}
            </div>

            <div className={s.messages}>
                {messageElements}
                <textarea onChange={onMessageChange}  value={props.newMessageText} />
                <div>
                <button onClick={props.addMessage}>Отправить</button>
                </div>
            </div>
        </div>
    )
};

export default Dialogs;