import { DialogsPageType } from "./store";



export type ActionsTypeDialogs = ReturnType<typeof addMessageAC> | ReturnType<typeof updateNewMessageTextAC>
let initialState = {
    dialogs: [
        {id: '1', name: 'Viktoria', img: '/img/avatar/avatar1.jpg'},
        {id: '2', name: 'Dmitriy', img: '/img/avatar/avatar2.jpg'},
        {id: '3', name: 'Sasha', img: '/img/avatar/avatar3.jpg'},
        {id: '4', name: 'Nikolay', img: '/img/avatar/avatar4.jpg'},
        {id: '5', name: 'Kris', img: '/img/avatar/avatar5.jpg'}
    ],
    messages: [
        {id: '1', message: 'Привет'},
        {id: '1', message: 'Как ты бро?'},
        {id: '1', message: 'Как ты бро?'},
        {id: '1', message: 'Как ты бро?'},
        {id: '1', message: 'Как ты бро?'},
        {id: '1', message: 'Да все хорошо'}
    ],
    newMessageText: ''
}
const dialogsReducer = (state: DialogsPageType =initialState, action: ActionsTypeDialogs) => {
    switch (action.type) {
        case "ADD_MESSAGE": {
            let newMessage = {
                id: '5',
                message: state.newMessageText
            };
            state.messages.push(newMessage);
            state.newMessageText = '';
            return {...state};
        }
        case "UPDATE_NEW_MESSAGE_TEXT": {
            state.newMessageText = action.newMessage;
            return {...state};
        }
        default: return {...state}

    }
}

export const addMessageAC = () => ({type: 'ADD_MESSAGE'}) as const;
export const updateNewMessageTextAC = (text: string) => ({
    type: 'UPDATE_NEW_MESSAGE_TEXT',
    newMessage: text
}) as const;
export default dialogsReducer;