import React from 'react';
import {addPostAC, updateNewPostTextAC,} from "../../../redux/profile-reducer"
import MyPosts from "./MyPosts";
import {connect} from "react-redux";
import {ActionsType} from "../../../redux/redux-store";
import {RootStateType} from "../../../redux/store";

let mapStateToProps = (state: RootStateType) => {
    return {
        posts: state.profilePage.posts,
        newPostText: state.profilePage.newPostText
    }
}
let mapDispatchStateToProps = (dispatch: (action: ActionsType) => void) => {
    return {
        updateNewPostText: (post: string) => {
            dispatch(updateNewPostTextAC(post))
        },
        addPost: () => {
            dispatch(addPostAC())
        }
    }
}

const MyPostsContainer = connect(mapStateToProps, mapDispatchStateToProps)(MyPosts)

export default MyPostsContainer;