import React from 'react';
import s from './Post.module.css'

type PropsType = {
  message: string,
  likeCounts: number,
  img: string
}


const Post = (props: PropsType) => {
  return <div>
    <div className={s.item}>
      <img src={props.img} alt=""/>
      {props.message}
      <div>
        <span>{props.likeCounts}</span>
      </div>
      </div>
  </div >
}

export default Post